.quality-control {

  h1 {
    margin-bottom: 5rem;
    text-align: center;
    font-size: 2.8rem;
    font-weight: 700;
    line-height: 1.2;
    color: var(--black-primary);

    @media (min-width: 1024px) {
      font-size: 4rem;
    }

    @media (min-width: 1200px) {
      margin-bottom: 5rem;
      font-size: 5rem;
    }

    @media (min-width: 1440px) {
      font-size: 6rem;
    }
  }

  &__text {

    h3 {
      margin-bottom: 3rem;
      text-align: center;
      font-size: 2rem;
      font-weight: 700;
      line-height: 1.2;
      color: var(--black-secondary);

      @media (min-width: 1200px) {
        text-align: left;
        margin-bottom: 6rem;
        font-size: 2.4rem;
      }
    }

    p {
      margin-bottom: 2.95rem;
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 1.2;
      color: var(--black-secondary);

      @media (min-width: 1200px) {
        font-size: 1.8rem;
      }

      &:last-of-type {
        margin-bottom: 2.95rem;

        @media (min-width: 1200px) {
          margin-bottom: 4rem;
        }
      }
    }

    ul {

      li {
        padding: 2.2rem 1.4rem;
        font-size: 1.6rem;
        font-weight: 400;
        line-height: 1.2;
        color: var(--black-secondary);

        @media (min-width: 1200px) {
          padding: 3.2rem 2.4rem;
          font-size: 1.8rem;
        }

        &:nth-of-type(odd) {
          border-radius: 1rem;
          background: rgba($blue-third, 0.08);
        }
      }
    }
  }
}